@keyframes fade-up {
    0% {
        opacity: 0;
        transform: translateY(100px);
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes fade-left {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }

    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes fade-right {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }

    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
        
    }

    100% {
        opacity: 1;
    }
}

@keyframes fade-out {
    100% {
        opacity: 1;
        
    }

    100% {
        opacity: 0;
    }
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    
}

html, body {
    width: 100%;
    overflow-x: clip;
    background: #120019;
}

.topbar-btn {
    width: 4rem;
    margin: 3px;
    background: none;
    border: none;
}

.topbar-btn:hover {
    background: none;
}

.topbar-btn:hover .menu-icon {
    color: #66fcf1;
}

.menu-icon {
    font-size: 3rem;
    cursor: pointer;
    color: white;
    transition: color 0.5s;
    animation: fade-in 1s;
}

.blur {
    filter: blur(10px);
}

.app {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;     
    gap: 40px; 
    padding: 20px;
    max-width: 1600px;
    margin: 0 auto;
}

.card-wrapper {
  display: block;              
  text-decoration: none;
  flex: 1 1 420px;            
  width: 100%;
}

.welcome-container {
    background: linear-gradient(to bottom right, #1a0033, #33001b, #1a0033);
    min-height: 100vh;
}

.hero-section {
    text-align: center;
    padding: 60px 20px 40px;
    color: white;
    font-family: 'Russo One', sans-serif;
}

.hero-section h1 {
    font-size: 3rem;
    margin-bottom: 10px;
    animation: fade-left 1s;
}

.hero-section p {
    font-size: 1.2rem;
    margin-bottom: 20px;
    font-family: 'Kalam', cursive;
    animation: fade-right 1.5s;
}

.cta-button {
    background-color: #ffffff;
    padding: 10px 25px;
    border-radius: 25px;
    text-decoration: none;
    color: #1a0033;
    font-weight: bold;
    transition-property: background-color;
    transition-duration: 0.5s;
    transition-timing-function: ease;
    cursor: pointer;
    animation: fade-in 2.5s;
}

.cta-button:hover {
    background-color: #80fefe;
}

.topbar {
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    background-color: rgba(26, 0, 51, 0.95);
    backdrop-filter: blur(7px);
    -webkit-backdrop-filter: blur(7px);
    padding: 10px 20px;
    z-index: 1000;
    justify-content: space-between;
}

.topbar-left {
    display: flex;
    align-items: center;
    gap: 8px;
    background: transparent;
    padding: 0;
}

.contact {
    text-decoration: none;
    font-family: 'Russo One';
    font-size: 1.5rem;
    color: white;
    margin-right: 15px;
    transition: color 0.5s;
}

.contact:hover {
    color: #66fcf1;
}

.site-name {
    color: white;
    font-size: 2rem;
    margin-left: 0px;
    font-family: 'Russo One';
}


@media (prefers-reduced-motion: no-preference) {
    * {
      scroll-behavior: smooth;
    }
}

    .scroll-to-top {
        position: fixed;
        bottom: 40px;
        right: 30px;
        z-index: 1000;
        background-color: #80fefe;
        color: #1a0033;
        border: none;
        border-radius: 50%;
        width: 45px;
        height: 45px;
        font-size: 1.5rem;
        font-weight: bold;
        cursor: pointer;
        box-shadow: 0 4px 8px rgba(0,0,0,0.3);
        animation: fade-up 0.5s;
    }

    .scroll-to-top:hover {
        background-color: #66fcf1;
    }


@media (max-width: 480px) {
    .site-name {
        font-size: 1.5rem;
        white-space: nowrap;
    }

    .contact {
        font-size: 1.2rem;
    }

    .topbar-left {
        gap: 10px;
        min-width: 0;
    }

    .menu-icon {
        font-size: 2rem;
    }

    .topbar {
        padding: 10px 0px;
        position: -webkit-sticky;
    }

    .topbar-btn {
        width: 3rem;
        margin: 3px;
        background: none;
        border: none;
    }
    
    .scroll-to-top {
        display: flex;
        justify-content: center;
    }
}


@media screen and (min-width: 480px) and (max-width: 768px) {
    .site-name {
        font-size: 1.5rem;
        white-space: nowrap;
    }

    .contact {
        font-size: 1.2rem;
    }

    .topbar-left {
        gap: 10px;
        min-width: 0;
    }

    .menu-icon {
        font-size: 2rem;
    }

    .topbar {
        padding: 10px 0px;
        position: -webkit-sticky;
    }

    .topbar-btn {
        width: 3rem;
        margin: 3px;
        background: none;
        border: none;
    }
    
    .scroll-to-top {
        display: flex;
        justify-content: center;
    }
}