@import url('https://fonts.googleapis.com/css2?family=Anta&family=Kalam:wght@400;700&family=Russo+One&display=swap');


* {
    margin: 0;
    box-sizing: border-box;
    padding: 0;
}

.navbar {
    height: 100vh;
    width: 100%;
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    background-image: linear-gradient(blueviolet, rgb(228, 52, 122));
    display: flex;
    flex-direction: column;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease-in-out, visibility 0s 0.5s;
}

.visible {
    opacity: 0.9;
    visibility: visible;
    transition: opacity 0.5s ease-in-out, visibility 0s;
}

.navbar ul {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    list-style: none;
}

.navbar a {
    color: inherit;
    text-decoration: none;
}

.navbar li {
    color: white;
    padding: 20px 40px;
    width: 400px;
    text-align: center;
    border-bottom: 1px solid white;
    font-family: 'Anta', sans-serif;
    font-size: larger;
}

.navbar li:hover {
    color: #80fefe;
    transition: color 0.5s;
}

button {
    width: fit-content;
    margin: 10px;
    background: none;
    border: none;
}

.close-icon {
    font-size: 2.5rem;
    cursor: pointer;
    color: white;
}

.close-icon:hover {
    color: #80fefe;
    transition: color 0.5s;
}

@media (max-width: 480px) {
    .navbar li {
        width: 300px;
    }
}

@media screen and (min-width: 480px) and (max-width: 768px) {
    .navbar ul {
        margin-top: 55px;
    }

    .navbar li {
        padding: 10px 10px;
    }
}